body {
  margin: 0;
}
/* font-family: 'Alata', sans-serif; */
.header {
  position: relative;
}
.img-head {
  width: 100%;
  height: 100%;
}
.navbar {
  position: absolute;
  top: 0;
  left: 0;
  margin: 3% 8% 2% 8%;
}
.nav-links {
  position: absolute;
  top: 0;
  left: 400%;
  margin-top: 0.4rem;
}
.nav-items {
  color: hsl(0, 0%, 100%);
  /* font-family: 'Josefin Sans', sans-serif; */
  font-family: "Alata", sans-serif;
  text-decoration: underline 0.15em rgba(0, 0, 0, 0);
  transition: text-decoration-color 300ms, text-underline-offset 300ms;
  margin-left: 3%;
  margin-right: 3%;
}
.nav-items:hover {
  text-decoration-color: hsl(0, 0%, 100%);
  text-underline-offset: 0.2em;
}
.head-text {
  border: 2px solid hsl(0, 0%, 100%);
  width: 35%;
  height: 35%;
  position: absolute;
  top: 30%;
  left: 8%;
}
.immersive {
  font-family: "Josefin Sans", sans-serif;
  color: hsl(0, 0%, 100%);
  font-weight: 300;
  font-size: 300%;
  word-wrap: break-word;
  text-transform: uppercase;
  padding-left: 5%;
  margin-top: 5%;
  padding-right: 5%;
}

/*Body*/

.container-body {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: 10% 15% 8% 15%;
}

.cover-txt {
  background-color: white;
  padding: 5% 0 0 10%;
  align-self: end;
  position: relative;
  left: -20%;
}
.inter-ract {
  width: 100%;
}
.leader {
  font-family: "Josefin Sans", sans-serif;
  color: hsl(0, 0%, 0%);
  font-weight: 300;
  font-size: 2rem;
  word-wrap: break-word;
  text-transform: uppercase;
}
.founded {
  font-family: "Alata", sans-serif;
  color: hsl(0, 0%, 55%);
  font-size: 0.75rem;
  margin-right: 18%;
}
.creations {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  margin: 10% 15% 10% 15%;
  grid-gap: 1.5rem;
  grid-template-areas: "our" "our" "our" "see";
}
.our-create {
  font-family: "Josefin Sans", sans-serif;
  color: hsl(0, 0%, 0%);
  font-weight: 300;
  font-size: 2rem;

  text-transform: uppercase;
}
.see-all {
  background-color: transparent;
  border: 1px solid hsl(0, 0%, 55%);
  width: 40%;
  padding-top: 10px;
  padding-bottom: 8px;
  font-family: "Alata", sans-serif;
  color: hsl(0, 0%, 0%);
  font-weight: 400;
  font-size: 0.9rem;
  border-radius: 3px;
  letter-spacing: 0.2rem;

  text-transform: uppercase;
}
.see-all:hover {
  background-color: black;
  border: 1px solid black;
  color: white;
  cursor: pointer;
}
.but-see {
  grid-column: 4/5;
  text-align: right;
  align-self: center;
}
.crea {
  grid-column: 1/4;
}
.create-img {
  width: 100%;
}
.create-things {
  position: relative;
}
.create-things:hover {
  opacity: 0.5;
  cursor: pointer;
}

.title-create {
  position: absolute;
  top: 70%;
  font-family: "Josefin Sans", sans-serif;
  color: hsl(0, 0%, 100%);
  font-weight: 300;
  font-size: 2rem;
  word-wrap: break-word;
  padding-left: 10%;

  text-transform: uppercase;
}
.title-create:hover {
  color: black;
  cursor: pointer;
}

/*Footer*/
.footer {
  background-color: black;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  padding: 5% 8% 2% 8%;
  grid-row-gap: 2rem;
}
.footer-links {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
}
.nav-footer {
  color: hsl(0, 0%, 100%);
  /* font-family: 'Josefin Sans', sans-serif; */
  font-family: "Alata", sans-serif;
  text-decoration: underline 0.15em rgba(0, 0, 0, 0);
  transition: text-decoration-color 300ms, text-underline-offset 300ms;
  padding-top: 5%;
}
.nav-footer:hover {
  text-decoration-color: hsl(0, 0%, 100%);
  text-underline-offset: 0.2em;
}
.socials {
  color: white;
  text-align: right;
}
.social-links {
  margin-right: 3%;
  text-decoration: underline 0.15em rgba(0, 0, 0, 0) !important;
  transition: text-decoration-color 300ms, text-underline-offset 300ms !important;
}
.social-links:hover {
  text-decoration-color: hsl(0, 0%, 100%) !important;
  text-underline-offset: 0.2em;
  cursor: pointer;
}
.copyright {
  color: white;
  text-align: right;
}
.copyright-txt {
  color: hsl(0, 0%, 100%, 60%);
  /* font-family: 'Josefin Sans', sans-serif; */
  font-family: "Alata", sans-serif;
  margin-top: 0;
  font-size: 0.8rem;
}
.hamburger {
  visibility: hidden;
}
.but-see-small {
  visibility: hidden;
}
.nav-body {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0);

  visibility: hidden;
}

@media (min-width: 1024px) and (max-width: 1223px) {
  .nav-links {
    left: 290%;
  }
  .head-text {
    width: 40%;
  }
  .immersive {
    font-size: 250%;
  }
  .see-all {
    width: 50%;
    letter-spacing: 0.1rem;
  }
  .title-create {
    font-size: 1.7rem;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  /*
  ##Device = Tablets, Ipads (landscape)
  ##Screen = B/w 768px to 1024px
*/
}
@media (min-width: 481px) and (max-width: 767px) {
  /* ##Device = Low Resolution Tablets, Mobiles (Landscape)
   ##Screen = B/w 481px to 767px */
}

@media (min-width: 320px) and (max-width: 1023px) {
  /*
    ##Device = Most of the Smartphones Mobiles (Portrait)
    ##Screen = B/w 320px to 479px
  */
  .hamburger {
    visibility: visible;
    position: absolute;
    top: 0;
    left: 150%;
    margin-top: 0.4rem;
    cursor: pointer;
  }
  .navbar {
    margin-top: 8%;
  }
  .nav-links {
    display: none;
  }
  .img-head {
    height: 500px;
  }
  .head-text {
    width: 85%;
  }
  .immersive {
    font-size: 220%;
  }
  .container-body {
    display: grid;
    grid-template-columns: 1fr;
    margin: 10% 5% 8% 5%;
  }

  .cover-txt {
    padding: 0 0 0 0;
    justify-self: center;
    text-align: center;
    position: relative;
    left: 0;
  }

  .founded {
    font-size: 1rem;
    margin-right: 0;
    padding-left: 5%;
    padding-right: 5%;
  }
  .creations {
    grid-template-columns: 1fr;
    margin: 10% 5% 10% 5%;
    grid-gap: 1.5rem;
    justify-content: center;
  }
  .but-see {
    display: none;
  }
  .but-see-small {
    visibility: visible;
    text-align: center;
  }
  .crea {
    grid-column: 1/-1;
    justify-self: center;
  }
  .footer {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    grid-row-gap: 1.5rem;
  }
  .logo-foot {
    margin-top: 5%;
    height: 50px;
    text-align: center;
  }
  .footer-links {
    grid-row: 2;
    display: grid;
    grid-template-columns: 1fr;
    text-align: center;
    margin-bottom: 5%;
  }
  .nav-footer {
    padding-top: 5%;
  }
  .socials {
    grid-row: 3;
    text-align: center;
    grid-area: "social";
  }
  .social-links {
    margin-right: 5%;
  }
  .copyright {
    color: white;
    text-align: center;
  }
  .copyright-txt {
    margin-top: 0;
    font-size: 0.8rem;
  }
  .title-create {
    position: absolute;
    top: 30%;
    font-family: "Josefin Sans", sans-serif;
    color: hsl(0, 0%, 100%);
    font-weight: 300;
    font-size: 1.6rem;
    word-wrap: break-word;
    padding-left: 10%;
    padding-right: 40%;

    text-transform: uppercase;
  }
  .nav-body {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.986);
    z-index: 2;
    visibility: visible;
  }
  .hello {
    color: white;
  }
  .top-body {
    margin-top: 8%;
    margin-left: 8%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    visibility: visible;
  }
  .img-close {
    justify-self: right;
    padding-right: 23%;
  }
  .close {
    padding-top: 3%;
    cursor: pointer;
  }
  .nav-links-top {
    margin-top: 20%;
  }
  .nav-items-top {
    color: hsl(0, 0%, 100%);
    display: block;
    font-family: "Josefin Sans", sans-serif;

    text-decoration: underline 0.15em rgba(0, 0, 0, 0);
    transition: text-decoration-color 300ms, text-underline-offset 300ms;
    padding-left: 10%;
    margin-bottom: 5%;
    font-size: 2.5rem;
  }
  .nav-items-top:hover {
    text-decoration-color: hsl(0, 0%, 100%);
    text-underline-offset: 0.2em;
  }
}
